/*color variables*/
$clr-primary: #600074;
$clr-primary-hover: #29e6a7;
$clr-primary-dark: #039d69;
$clr-gray100: #f9fbff;
$clr-gray150: #f4f6fb;
$clr-gray200: #eef1f6;
$clr-gray300: #e1e5ee;
$clr-gray400: #767b91;
$clr-gray500: #4f546c;
$clr-gray600: #2a324b;
$clr-gray700: #161d34;


$radius: 0.2rem;

.wrapperca {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(12rem, 16rem));
    gap: 2rem;
    justify-content: center;
    margin-top: 2rem;
}

.card {
    background-color: rgb(248, 239, 255);
    overflow: hidden;
    box-shadow: 5px 3px $clr-gray300;
    border-radius: $radius;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: transform 200ms ease-in;

    &__title {
        padding: 1rem;
    }

    &__description {
        padding: 0 1rem;
    }

    &__btn {
        padding: 1rem;
        font-family: inherit;
        font-weight: bold;
        font-size: 1rem;
        margin: 1rem;
        border: 2px solid $clr-primary;
        background: transparent;
        color: $clr-primary;
        border-radius: $radius;
        transition: background 200ms ease-in, color 200ms ease-in;
    }

    &:hover {
        transform: scale(1.02);
    }

    &:hover &__btn {
        background: $clr-primary;
        color: white;
    }
}